<template>
    <div class="main-page">
        <div v-if="show">
            <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
                <van-swipe-item v-for="(image, index) in $config.swipeList" :key="index">
                    <van-image :src="image" />
                </van-swipe-item>
            </van-swipe>

            <van-notice-bar :scrollable="false" background="#f4f6f8" color="#000">
                <div slot="left-icon">
                    <van-icon name="volume" color="#409eff" size="16px" :badge="noticeList.length" style="margin-right: 15px" />
                    <span style="font-weight: 600">通知</span>
                    <span style="margin: 0 10px; color: #dcdce3">|</span>
                </div>
                <van-swipe vertical class="notice-swipe" :autoplay="3000" :show-indicators="false">
                    <van-swipe-item class="hidden-one" v-for="(item, index) in noticeList" :key="index" @click="toNotice(item)">{{ item.Title }}</van-swipe-item>
                </van-swipe>
            </van-notice-bar>

            <div v-for="(item, index) in menu" :key="index">
                <div class="headerTag">
                    {{ item.MenuNameWap ? item.MenuNameWap : item.MenuName }}
                </div>
                <van-grid :border="false">
                    <van-grid-item v-for="(item, index) in item.children" :key="index" @click="toPage(item)">
                        <van-image class="grid-img" width="52" :src="item.Logo ? item.Logo : '/static/img/logo/102.jpg'" />
                        <span class="menu-txt">{{
              item.MenuNameWap ? item.MenuNameWap : item.MenuName
            }}</span>
                    </van-grid-item>
                </van-grid>
            </div>
        </div>
        <div v-else class="load">
            <van-loading type="spinner" />
        </div>
    </div>
</template>

<script>
export default {
    name: "Home1",
    props: {},
    components: {},
    data() {
        return {
            menu: [],
            sons: [],
            noticeList: [],
            show: true,
        };
    },
    methods: {
        getMenu() {
            // this.menu = JSON.parse(this.$u.getItem("menu")).filter(it => !it.EleId && it.Url && it.Url.includes("oa"))
            this.menu = this.$u
                .arraytoTree(JSON.parse(this.$u.getItem("menu")).sort((a, b) => a.WapRowId - b.WapRowId))
                .filter((it) => it.EleId == "oa")[0].children;
            
        },
        toPage(item) {
            this.$router.push(item.Url);
            sessionStorage.setItem("pageName", item.MenuName);
            this.show = false;
        },
        toNotice(item) { },
        BD_MessageIndex() {
            this._api.BD_MessageIndex({ IsRead: 0 }).then((res) => {
                if (res) {
                    this.noticeList = res.data;
                }
            });
        },
    },
    created() {
        this.getMenu();
        this.BD_MessageIndex();
    },
};
</script>

<style lang="scss" scoped>
.main-page {
    .van-notice-bar {
        margin: 10px 10px 0 10px;
        border-radius: 3px;
    }

    .notice-swipe {
        height: 40px;
        line-height: 40px;
    }

    .van-grid-item {
        .grid-img {
            border-radius: 6px;
            overflow: hidden;
        }

        .menu-txt {
            font-size: 14px;
            margin-top: 5px;
            white-space: nowrap;
        }
    }
}
</style>
